import React from 'react'
import ReactDOM from 'react-dom'

import Header from '../Header'
import Gallery from '../Gallery'
import '../../styles/text.css'
import styles from '../../styles/home.module.css'
import createButton from '../../resources/createButton.png'
import galleryButton from '../../resources/GalleryButton.png'
import uploadButton from '../../resources/uploadButton.png'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

export default class Home extends React.Component {
    constructor(props){
        super(props);

        this.state = {ids: []};
    }

    componentDidMount(){
        requester.get('/api/expositions/top')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    let ids = this.state.ids;
                    ids = res.ids;
                    
                    console.log(ids);
                    this.setState({
                        ids
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }

    render(){

        return (
            <div>
                <Header transparent={false}></Header>
                <Gallery></Gallery>
                <div className={styles.content} ref='content'>
                    <div className={styles.item}>
                        <img src={createButton}></img>
                        <h1> </h1>
                        <span>        </span>
                    </div>
                    <div className={styles.item}>
                        <img src={galleryButton}></img>
                        <h1> </h1>
                        <span>        </span>
                    </div>
                    <div className={styles.item}>
                        <img src={uploadButton}></img>
                        <h1></h1>
                        <span>  ,    ,   </span>
                    </div>
                    <div className={styles.item}>
                        <h1>  VShow?</h1>
                        <span>
                            VShow       3D    , 3D ,  
                        </span>
                    </div>

                    <h1>- </h1>
                    
                </div>
            </div>
        )
    }
}